.navbar {
  /*background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(0, 0, 0) 100%);*/
  background: rgba(18, 18, 18, 1);
  /*background: #1F4E79;*/
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  max-width: 1500px;
}

.navbar-logo {
  color: #FFC700;
  justify-self: start;
  margin-left: 0px;
  cursor: pointer;
  text-decoration: none;
  text-shadow: 2px 2px rgba(128, 128, 128, 0.8);
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-motto {
  margin-left: 0px;
}

.navbar-logo-container {
  display: flex;
  align-items: center;
  grid-gap: 0px;
  gap: 0px;
}

.navbar-text-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  grid-gap: 2px;
  gap: 2px;
}

.navbar-logo-image {
  height: 70px; /* Adjust as needed */
  margin-left: 0px; /* Small margin to move logo slightly to the right */
  margin-bottom: 0px; /* Small margin to move logo slightly to the right */
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 30px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 4rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #FFC700;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #FFC700;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #FFC700;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 100px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #FFC700;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    color: #FFC700;
    justify-self: start;
    margin-left: 0px;
    cursor: pointer;
    text-decoration: none;
    text-shadow: 2px 2px rgba(128, 128, 128, 0.8);
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    padding-right: 50px; 
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 5px;
    right: 10px;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

